<template>
  <vs-prompt
    title="Add New Accessorial Fee"
    accept-text="Save"
    @accept="acceptAccessorialFeesPrompt"
    @cancel="closeAccessorialFeesPrompt"
    @close="closeAccessorialFeesPrompt"
    :is-valid="validateForm"
    :active.sync="isOpen"
    style="z-index: 52015"
  >
    <div class="con-exemple-prompt space-y-4">
      <vs-input
        v-validate="'required'"
        name="name"
        label="Name"
        v-model="name"
        class="w-full"
      />

      <vs-input
        v-validate="'required'"
        label="Description"
        name="description"
        v-model="description"
        class="w-full"
      />
    </div>
  </vs-prompt>
</template>

<script>
export default {
  name: 'AccessorialFeePrompt',

  props: {
    active: {
      required: true,
      default: false,
    },
  },

  data() {
    return {
      isOpen: this.active,
      name: '',
      description: '',
    };
  },

  computed: {
    validateForm() {
      return !this.errors.any() && this.name !== '' && this.description !== '';
    },
  },

  watch: {
    active(newVal) {
      this.isOpen = newVal;
    },
  },

  methods: {
    initialVal() {
      this.name = '';
      this.description = '';
    },

    closeAccessorialFeesPrompt(isAdded = false) {
      // this.active = false

      if (!isAdded) {
        // reseting input by the same function
        this.$emit('accessorial-fee::added', false);
      }

      this.initialVal();

      this.$emit('close');
    },

    acceptAccessorialFeesPrompt() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.$vs.loading();

          const payload = {
            name: this.name,
            description: this.description,
          };

          this.$store
            .dispatch('accessorial-fees/createAccessorialFee', payload)
            .then(({ payload }) => {
              // add fees in local array
              this.$emit('accessorial-fee::added', payload);

              this.closeAccessorialFeesPrompt(true);

              this.$vs.notify({
                color: 'primary',
                title: 'Accessorial Fee',
                text: 'Accessorial fee added successfully.',
              });
            })
            .catch((error) => {
              this.$vs.notify({
                title: 'Error',
                text: error.response.data.message,
                color: 'danger',
              });
            })
            .finally(() => this.$vs.loading.close());
        }
      });
    },
  },
};
</script>

<style scoped></style>
